import React, { Component } from 'react'
import './action.css'

export default class Action extends Component {
    render(){
        return (<div className='action-wrapper'>
            <h3 className='action-title'></h3>
            <div className='action-list'>
                {data.map(item => {
                    return (
                        <div className='action-item'>
                            <span className='action-item-name'>{item.name}</span>
                            <span className='action-item-action'>{actionMap[item.actionType]}</span>
                            <span className='action-item-data'>{`${item.data}`}</span>
                        </div>
                    )
                })}
            </div>
        </div>)
    }
}

const data = [{
    name: '',
    actionType: 1,
    data: Math.floor(Math.random() * 24)
},{
    name: '',
    actionType: 2,
    data: Math.floor(Math.random() * 24)
},{
    name: '',
    actionType: 3,
    data: Math.floor(Math.random() * 24)
}]

const actionMap = {
    1: '',
    2: '',
    3: '',
}